import React from "react"


export default class Form extends React.Component {
  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.state = {
      status: ""
    };
  }

  render() {
    const { status } = this.state;
    return (
      <section className="form-wrapper" data-sal="fade" data-sal-duration="200">
        <form
          onSubmit={this.submitForm}
          action="https://formspree.io/f/mgepbvwa"
          method="POST"
          className="form"
        >
          <label style={{ display: "block" }}>
            Name
          <input style={{ display: "block" }} type="text" name="name" />
          </label>
          <label style={{ display: "block" }}>
            Email
          <input style={{ display: "block" }} type="email" name="email" />
          </label>
          <label style={{ display: "block" }}>
            Your Message
          <textarea style={{ display: "block", width: "100%", height: "100px" }} name="message" id="message"></textarea>
          </label>

          {status === "SUCCESS" ? <p className="success">Thanks!</p> : <button className="btn btn-lg btn-outline-light" type="submit">Say Hi</button>}
          {status === "ERROR" && <p className="error" style={{ display: "block" }}>Ooops! There was an error.</p>}

        </form>
      </section>
    );
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.setState({ status: "SUCCESS" });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  }
}
